import React from 'react';


import AppService from '../app/AppService';
import ModalDialog from './ModalDialog';
import EntityPath from '../common/EntityPath';
import BuildingBlockMap from '../core/BuildingBlockMap';

class TypeSelector extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            showSelector: false,
            type: this.props.type,
            targetEntity: "",
            browseMode: true
        };

        this.bb = AppService.getBuiltInBlocks();
    }

    componentWillReceiveProps( nextProps ) {
        if ( nextProps.visible ) {
            this.browse( nextProps.type );
        }
    }

    listAllSubtypes(t, param) {
        const aliases = t.execute("listAliases");
        const subTypes = t.execute("listAllSubTypes", param);
        return BuildingBlockMap.mergeMaps(subTypes, aliases);
        
        /*
        const m1 = BuildingBlockMap.mergeMaps(subTypes, aliases);
        let listContained;
        try {
            listContained = t.execute("listContained", param);
        } catch (e) {}

        return listContained? BuildingBlockMap.mergeMaps(m1, listContained) : m1;
        */
    }

    listSubtypes(t, param) {
        const aliases = t.execute("listAliases");
        const subTypes = t.execute("listSubTypes", param);
        return BuildingBlockMap.mergeMaps(subTypes, aliases);
    }

    handlePageNavigation( parent ) {
        this.browse( parent );
    }
    browse( type ) {
        const entities = this.listSubtypes(type);
        this.setState( { type, entities, browseMode: true, targetEntity: "" });
    }
    formatName( entity, emphasize ) {
        if ( entity ) {
            const name = entity.execute( "getName" );
            if ( name ) {
                if ( emphasize ) {
                    return <strong>{ name }</strong>;
                } else {
                    return name;
                }
            } else {
                return <i>&lt; { entity.id } &gt; </i>;
            }
        }
    }
    handleSelectType( type ) {
        let shouldClose = true;
        if ( this.props.onSelect ) {
            if ( type ) {
                shouldClose = this.props.onSelect( type );
            } else {
                shouldClose = this.props.onSelect( this.state.targetEntity, this.state.type );
            }
        }
        if ( shouldClose ) {
            this.handleClose();
        } else {
            const targetEntity = "";
            this.setState( { targetEntity });
        }
    }
    selectType( type ) {
        if ( this.state.browseMode ) {
            this.handleSelectType( this.state.type );
        } else {
            this.handleSelectType( type );
        }
    }
    handleClose() {
        if ( this.props.onClose ) {
            this.props.onClose();
        }
        const targetEntity = "";
        this.setState( { targetEntity });
    }
    selectTerm( targetEntity ) {
        let results;
        if ( targetEntity ) {
            let newFinder = this.bb.types.newFinder;
            newFinder.execute( "setFilter", { term: targetEntity });
            //newFinder.execute( "setSort", { field: sort } );
            newFinder.execute( "clearFacets" );
            let entities = this.listAllSubtypes(this.state.type, newFinder);
            results = { count: entities.size, entities };
        }
        this.setState( { targetEntity, results, browseMode: false });
    }
    handleTargetEntitySelect( type ) {
        const targetEntity = type.execute( "getName" ) || type.id;
        this.selectTerm( targetEntity );
    }
    handleTargetEntityChange( e ) {
        const targetEntity = e.target.value;
        this.selectTerm( targetEntity );
    }
    buildRow( value, itself ) {
        let name = this.formatName( value );
        const subTypes = this.listSubtypes(value);
        let subTypeCount;
        if ( subTypes && subTypes.size ) {
            let subTypeCountLabel = subTypes.size;
            subTypeCountLabel += ( subTypes.size > 1 ) ? " subtypes" : " subtype";
            subTypeCount = <span className="count" title={subTypeCountLabel}>{subTypes.size}</span>;
        }

        let namePlaceholder = <span className="aLink" onClick={this.browse.bind( this, value ) }>{ name }</span>;
        if ( itself ) {
            namePlaceholder = name;
        }

        const type = <span className="row size" key={value.id}>
            <div className="main">{namePlaceholder}</div>
            <div className="details right">
                <span className="counters">{subTypeCount}</span>
            </div></span>;
        return type;
    }
    render() {
        if ( this.props.visible ) {
            const type = this.buildRow( this.state.type, true );
            let subTypes = [];
            if ( this.state.entities ) {
                this.state.entities.forEach(( value, key ) => {
                    const row = this.buildRow( value, false );
                    subTypes.push( row );
                });
            }
            let results = [];
            let resultsCount = 0;
            let searchButton = "Search";
            let browseMode = this.state.browseMode;
            let matchedEntity;
            if ( this.state.results ) {
                resultsCount = this.state.results.count;
                searchButton = "Create " + this.state.targetEntity;
                this.state.results.entities.forEach(( value, key ) => {
                    const resultTypeLabel = this.formatName( value );
                    const resultTypeName = value.execute( "getName" );
                    const resultTypeName2 = resultTypeName ? resultTypeName.toUpperCase() : null;
                    const targetEntity = this.state.targetEntity.trim().toUpperCase();
                    if ( targetEntity === resultTypeName2 || targetEntity === value.id.toUpperCase() ) {
                        matchedEntity = value;
                        results.push( <div key={value.id}><strong>{ resultTypeLabel }</strong></div> );
                        searchButton = <span className="button-label">Select { resultTypeLabel } </span>;
                    } else {
                        results.push( <div key={value.id}><span className="aLink" 
                            onClick={this.handleTargetEntitySelect.bind( this, value ) }>{ resultTypeLabel }</span></div> );
                    }
                });
            } else {
                browseMode = true;
            }

            //const root = window.builtInBlocks.root;
            //const typeName = (this.props.type && this.props.type.id != root.id)? this.formatName( this.props.type, true ) : "entity";
            const typeName = this.formatName( this.props.type, true );
            const selectionName = this.formatName( this.state.type, true );
            searchButton = ( browseMode ) ? <span className="button-label">Select {selectionName}</span> : searchButton;

            return (
                <ModalDialog onClose={this.handleClose.bind(this)}>
                    <div className="modal-content-title">Provide the {typeName} you want to add</div>
                    <div className="modal-content-container">
                        <div className="entitySearchForm">
                            <input
                                type="text"
                                autoFocus
                                className="entitySearchName"
                                placeholder="Entity name"
                                value={this.state.targetEntity}
                                onChange={this.handleTargetEntityChange.bind(this)}
                                />
                            <div className="actions">
                                <button className="btn" onClick={this.selectType.bind( this, matchedEntity ) }>
                                    { searchButton }
                                </button>
                            </div>
                        </div>
                        <div className="breadcrumbs">
                            <div className="title">Path: </div>
                            <div className="content">
                                <EntityPath data={this.state.type} base={this.props.type} type="parents" onClick={this.handlePageNavigation.bind(this)} />
                            </div>
                        </div>
                        { browseMode ?
                            <div className="browse-types">
                                <div className="row-list">
                                    <div className="head strong">
                                        {type}
                                    </div>
                                    <ul className="rows pos modal-content-height">
                                        {subTypes}
                                    </ul>
                                </div>
                            </div>
                            :
                            <div className="search-types">
                                <div className="row-list">
                                    <div className="head strong">
                                        <div className="row size">
                                            <div className="main">{ selectionName } type containing '{this.state.targetEntity}'</div>
                                            <div className="details right">
                                                <span className="counters">
                                                    <span className="icon-base number e-type">{ resultsCount }</span>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                    <ul className="rows pos modal-content-height">
                                        {results}
                                    </ul>
                                    <button className="btn" onClick={this.browse.bind( this, this.state.type ) }>
                                        Back to { selectionName }
                                    </button>
                                </div>
                            </div>
                        }
                    </div>
                </ModalDialog> );
        } else {
            return null;
        }
    }
}

export default TypeSelector



