import React from 'react';

import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import IdInput from '../../common/IdInput';
import utils from '../../core/utils';
import '../View.css';

class HasAlias extends ViewComponent {
  constructor(props) {
    super(props);
    this.state = {
        showSelector: false
    };
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onDelete(ref) {
    console.log('Delete HAS '+ ref);
    //TODO, check if any member with this tag first..
    this.props.entity.execute("deleteAlias", ref.id, this.props.component);

    this.closeSelector();
    this.props.refresh();
  }

  onCreate(entityName) {
    const entityId = utils.generateId(entityName);
    const newEntity = this.props.entity.execute("newAlias", entityId);
    newEntity.execute("setName", entityName);

    this.closeSelector();
    this.props.refresh();
  }

  onAdd(ref) {
    this.setState((prevState) => {
        return { showSelector: true };
    });
  }

  renderAliases(aliases) {
    if (aliases && aliases.size > 0) {
      let list = [];
      for (let bId of aliases.keys()) {
        const alias = aliases.get(bId);
        const name = this.formatName(alias);
        list.push(<li key={alias.id} className="browse-behavior-row"><span 
          onClick={this.goTo.bind(this, alias.id)} className="link">{name}</span>
            <span key="deleteOveride" className="row-right"><button 
              className="btn-add" onClick={this.onDelete.bind(this, alias)}><i 
              className="fa fa-minus red"></i></button></span>
          </li>);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component) + this.getTagName();
    const aliases = this.props.entity.execute("listAliases");
    const count = aliases && aliases.size;
    this.props.onCount && this.props.onCount(count);
    const list = this.renderAliases(aliases);
    const uid = this.bb.generateUID();
    return (
      <div className='browse-behavior-container'>
        <BrowseBehaviorBox title={name} count={count}
          onAdd={this.onAdd.bind(this)}>
          {list?
            <ul className="browse-behavior-rows">
              {list}
            </ul>
            : <div className="browse-behavior-row">No Aliases</div>}
        </BrowseBehaviorBox>
        <IdInput key={uid}
          visible={this.state.showSelector}
          onClose={this.closeSelector.bind(this)}
          onCreate={this.onCreate.bind(this)}/>
      </div>
    );
  }
}

export default HasAlias;


