import React, { Component } from 'react';

import '../style/Common.css';

class AppFooter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            status: "idle",
            notifications: 0
        };
    }

    testOnline() {
        this.setState(function(prevState, props) {
            let newStatus;
            if (prevState.status === "online") {
                newStatus = "idle";
            } else if (prevState.status === "offline") {
                newStatus = "online";
            } else {
                newStatus = "offline";
            }
            return {
                status: newStatus
            };
        });
    }

    testNotifications() {
        let n = (this.state.notifications + 1) %3;
        this.setState({ notifications: n } );
    }

    render() {
        const version = "0.7.0";

        const online = this.state.status;
        const notifications = this.state.notifications;

        const notificationClass = "fa fa-bell " + (notifications? "red fa-blink" : "green");
        const notificationMessage = (notifications)? notifications + " new notifications" : "Notifications";

        let onlineClass, onlineMessage;
        if (online === "online" ) {
            onlineClass = "fa fa-cloud blue";
            onlineMessage = "Online";
        } else if (online === "offline" ) {
            onlineClass = "fa fa-spinner fa-spin red";
            onlineMessage = "Offline";
        } else if (online === "idle" ) {
            onlineClass = "fa fa-cloud lightblue";
            onlineMessage = "Online, idle";
        } else {
            onlineClass = "fa fa-cloud gray";
            onlineMessage = "Unknown status";
        }
        return <div className="page-footer">
            <div className="page-footer-label">
                <div className="status">
                    <i className={notificationClass} onClick={this.testNotifications.bind(this)} title={notificationMessage}></i> &nbsp; 
                    <i className={onlineClass} onClick={this.testOnline.bind(this)} title={onlineMessage}></i>
                </div>
                Semantic Types version {version}
            </div>
            <div className="page-footer-copyright">Copyright  2017 semantictypes.com. All rights reserved.</div>
        </div>;
    }
}

export default AppFooter;


