import React from 'react';

import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import TypeSelector from '../../common/TypeSelector';
import AliasConnection from '../../core/AliasConnection';
import '../View.css';

class SimpleTypePresenter extends ViewComponent {

  constructor(props, typeID, typeIDs, typeName) {
    super(props);
    this.state = {
        showSelector: false
    };
    this.typeID = typeID;
    this.typeIDs = typeIDs;
    this.typeName =typeName;
    this.setProps(props)
  }

  componentWillReceiveProps(nextProps) {
    this.setProps(nextProps);
  }

  setProps(props) {
    this.theEntity = (props.entity instanceof AliasConnection)? props.entity.entity : props.entity;
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onAddParentType(data, base) {
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    this.theEntity.execute("add" + this.typeID, entity);
    this.closeSelector();
    this.props.refresh();
  }

  onDelete(param) {
    this.theEntity.execute("remove" + this.typeID, param.id);
    this.closeSelector();
    this.props.refresh();
  }

  onAddType(ref) {
    const selectorType = this.bb.systemRoot.execute( "getEntity", 'userEntity' );
    this.showConnectionSelector(selectorType, this.onAddParentType);
  }

  showConnectionSelector( selectorType, aSelectorFn ) {
    const selectorFn = aSelectorFn.bind(this);
    this.setState((prevState) => {
        return { showSelector: true, selectorType, selectorFn };
    });
  }

  renderParentTypes(parentTypes) {
    if (parentTypes && parentTypes.size > 0) {
      let list = [];
      for (let bId of parentTypes.keys()) {
        const parentType = parentTypes.get(bId);
        const name = this.formatName(parentType);
        list.push(<li key={parentType.id} className="browse-behavior-row"><span className="link"
          onClick={this.goTo.bind(this, parentType.id)}>{name}</span>
          <span key="deleteOveride" className="row-right"><button 
            className="btn-add" onClick={this.onDelete.bind(this, parentType)}><i 
            className="fa fa-minus red"></i></button></span>
          </li>);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component);
    const parentTypes = this.theEntity.execute("list" + this.typeIDs);
    const count = parentTypes && parentTypes.size;
    this.props.onCount && this.props.onCount(count);

    const list = this.renderParentTypes(parentTypes);
    return (
      <div className='browse-behavior-container'>
        <BrowseBehaviorBox title={name} count={count}
            onAdd={this.onAddType.bind(this)}>
          {list?
            <ul className="browse-behavior-rows">
              {list}
            </ul>
            : <div className="browse-behavior-row">No {this.typeName}s</div>}
        </BrowseBehaviorBox>
        <TypeSelector
          visible={this.state.showSelector}
          onClose={this.closeSelector.bind(this)}
          onSelect={this.state.selectorFn}
          type={this.state.selectorType} />
      </div>
    );
  }
}

export default SimpleTypePresenter;


