import React from 'react';

import { withRouter } from 'react-router'
import { Link } from 'react-router-dom';

import '../style/App.css';
import AppService from './AppService';
import AppMenu from '../common/AppMenu';
import AppFooter from '../common/AppFooter';
import Logo from '../common/Logo';
import TitleBox from '../common/TitleBox';
import SignInMenu from '../common/SignInMenu';
import EntityPath from '../common/EntityPath';
import DropDown from '../common/DropDown';
import EntityGraph from '../common/EntityGraph';
import ViewComponent from '../common/ViewComponent';

class ViewEntityGraph extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        isDropDownMenuShown: false,
        reference: false
    };
    this.pageFocus =  true;

    this.bb = AppService.getBuiltInBlocks();
    this.setEntity(this.props);
  }

  componentWillUnmount() {
    this.pageFocus =  false;
  }

  componentWillReceiveProps(nextProps) {
    this.setEntity(nextProps);
  }

  setEntity(props) {
    const entityId = props.match.params.id;
    this.entity = this.bb.systemRoot.execute( "getEntity", entityId ) || this.bb.systemRoot;
  }

  onRefresh() {
    // TODO: force refresh the graph
  }

  showDropDownMenu() {
    this.setState((prevState) => {
        return {
            isDropDownMenuShown: !prevState.isDropDownMenuShown
        }
    });
  }

  closeDropDownMenu() {
    if (this.pageFocus) {
        this.setState((prevState, props) => {
            return { isDropDownMenuShown: false };
        });
    }
  }

  onReferenceMode() {
    this.setState((prevState) => {
        return {
            reference: true
        }
    });
  }

  onInheritanceMode() {
    this.setState((prevState) => {
        return {
            reference: false
        }
    });
  }

  handleEntitySelected(id) {
    this.props.history.push('/graph/' + id);
  }

  renderMenu(entity) {
    let inheritanceClass = 'dropdown-menu-list-item-option';
    let referenceClass = 'dropdown-menu-list-item-option';
    if (this.state.reference) {
      referenceClass = 'dropdown-menu-list-item-option-selected';
    } else {
      inheritanceClass = 'dropdown-menu-list-item-option-selected';
    }
    return (
      <span className="header-entry">
        <button className="header-button" onClick={this.showDropDownMenu.bind(this)}><i className="fa fa-cog fa-lg"></i></button>
        <DropDown visible={this.state.isDropDownMenuShown} onClose={this.closeDropDownMenu.bind(this)}>
          <div className="dropdown-menu">
            <ul className="dropdown-menu-list">
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Layout:</span>
                <ul>
                  <li className="dropdown-menu-list-item-option-selected"><Link 
                    to={'/view/' + entity.id}>List</Link></li>
                  <li className="dropdown-menu-list-item-option"><Link 
                    to={'/workspace/' + entity.id}>Workspace</Link></li>
                  <li className="dropdown-menu-list-item-option"><Link 
                    to={'/graph/' + entity.id}>Graph</Link></li>
                </ul>
              </li>
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Relationships:</span>
                <span className={inheritanceClass} onClick={this.onInheritanceMode.bind(this)}>Inheritance</span>
                <span className={referenceClass} onClick={this.onReferenceMode.bind(this)}>Reference</span>
              </li>
            </ul>
          </div>
        </DropDown>
      </span>
    );
  }

  renderGraph(entity) {

    const entityId = this.props.match.params.id;
    if (this.state.reference) {
      EntityGraph.defaultProps = {
        width: 800,
        height: 800,
        linkDistance: 10,
        forceStrength: -10
      };
    } else {
      EntityGraph.defaultProps = {
        width: 800,
        height: 800,
        linkDistance: 4,
        forceStrength: -8
      };
    }

    let nodes = [];
    let idx = {'guest-root': 0}, i = 0;
    for (let t in this.bb.types) {
      let type = this.bb.types[t];
      if (type) {
        idx[t] = i;
        const ancestors = type.execute("listAncestors");
        const ancestorsSize = ancestors.size || 1;
        let c = Math.round(14/(ancestorsSize));
        let color = (ancestorsSize === 1)? '#f80' : '#' + c + '' + (c+1) + '' + (c+2);
        const superTypes = type.execute("listSuperTypes");
        if (superTypes && superTypes.size && superTypes.has("aliasType")) {
          color = '#798';
          c = Math.round(Math.sqrt(c))+1;
        }

        let dColor = color;
        if (t === entity.id) {
          color = 'red';
        }
        nodes.push({
          id: i,
          name: t,
          r: 0 + c,
          depth: ancestorsSize,
          color: color,
          dColor: dColor
          //group: (Math.random() * 5 ) + 1
        });
        i++;
      } else {
        console.error('Missing: ' + t);
      }
    }

    let links = [];
    i = 0;
    for (let t in this.bb.types) {
      console.log('type: '+ t);
      let typeT = this.bb.types[t];
      if (!typeT) {
          console.error('Cannot find ' + t);
          continue;
      }
      let tt = idx[t];
      if (this.state.reference) {
        try {
          const refTypes = typeT.execute("listConnections");
          if (refTypes && refTypes.size > 0) {
            for (let bId of refTypes.keys()) {
              const refType = refTypes.get(bId);
              //console.log(t + ' -> (' + refType.tag.id + ') -> ' + refType.id);

              if (refType.tag.id !== '___') {
                let refTypeId = refType.id.replace('.', '');
                const target = idx[refTypeId];
                if (target != null) {
                  if (i !== target) {
                    //console.log(tt + ' -> ' + target);
                    links.push({
                      source: tt,
                      target,
                      color:'#4c8'
                    });
                  }
                } else {
                  console.error('Missing entity: ' + refType.id);
                }
              }
            }
          }
        } catch (err) {
          console.error('Error: ' + err);
        }
      } else {
        const superTypes = typeT.execute("listSuperTypes");
        if (superTypes && superTypes.size > 0) {
          for (let bId of superTypes.keys()) {
            const superType = superTypes.get(bId);
            let superTypeId = superType.id.replace('.', '');
            const target = idx[superTypeId];
            if (target != null) {
              if (i !== target) {
                links.push({
                  source: tt,
                  target,
                  color:'#789'
                });
              }
            } else {
              console.error('Missing entity: ' + superType.id);
            }
          }
        }
        const subTypes = typeT.execute("listSubTypes");
        if (subTypes && subTypes.size > 0) {
          for (let bId of subTypes.keys()) {
            const subType = subTypes.get(bId);
            let subTypeId = subType.id.replace('.', '');
            const target = idx[subTypeId];
            if (target != null) {
              if (i !== target) {
                links.push({
                  source: tt,
                  target,
                  color:'#f84'
                });
              }
            } else {
              console.error('Missing entity: ' + subType.id);
            }
          }
        }
      }      
      i++;
    }
    return <EntityGraph key={this.state.reference} entityId={entityId} nodes={nodes} links={links} onClick={this.handleEntitySelected.bind(this)}/>;
  }

  render() {
    const name = this.formatName(this.entity);
    const graph = this.renderGraph(this.entity);

    return (
      <div className="App">
        <div className="App-header">
          <div className="d-flex px-3 flex-justify-between container-lg">
            <div>
              <Logo />
              <TitleBox name={name}/>
            </div>
            <div className="d-flex">
              <AppMenu refresh={this.onRefresh.bind(this)} />
              <SignInMenu />
            </div>
          </div>
        </div>
        <div className="App-intro">
          <div className="d-flex px-3 flex-justify-between container-lg">
              <div className="bc-path">
                Path: <EntityPath data={this.entity} />
              </div>
              <div className="d-flex">
                { this.renderMenu(this.entity) }
              </div>
          </div>
          <div className="main-view">
            { graph }
          </div>
        </div>
        <div className="App-footer">
          <AppFooter />
        </div>
      </div>
    );
  }
}

export default withRouter(ViewEntityGraph);



