
/**
 * AliasConnection.
 */
import BuildingBlockMap from './BuildingBlockMap';
import CoreBuildingBlock from './CoreBuildingBlock'

export default class AliasConnection extends CoreBuildingBlock {
    constructor(id, entity) {
        super(entity);
        this.id = id;
        this.entity = entity;
        this.data = entity.data;

        const thisInstance = CoreBuildingBlock.bootstrappEntity;
        thisInstance.data.set(id, this);
        thisInstance._addToList.bind(entity)(this, "aliases");
    }

    execute(action, param, ...extraParams) {
        const thisInstance = CoreBuildingBlock.bootstrappEntity;
        if (action === "setName") {
            this.aliasName = param;
            return;
        } else if (action === "getName") {
            return this.aliasName;
        } else if (action === "getAliasOrigin") {
            return this.entity;
        }
        let result = this.entity.execute.bind(this)(action, param, ...extraParams);
        if (action === "listSuperTypes") {
            const aliasType = thisInstance.data.get('aliasType');
            let aliasTypeList = new BuildingBlockMap();
            aliasTypeList.set(aliasType.id, aliasType);
            result = BuildingBlockMap.mergeMaps(result, aliasTypeList);
        } else if (action === "listAllBehaviors") {
            const isAlias = thisInstance.data.get('isAlias');
            const aliasSuperTypes = isAlias.execute("listSuperTypes");
            //TODO: 'remove' the hasAlias for aliases from the list
            // only add isAlias for non-tag calls or for the proper parent tag: 
            if (!param || (aliasSuperTypes && aliasSuperTypes.has(param))) {
                let aliasBehavior = new BuildingBlockMap();
                aliasBehavior.set(isAlias.id, isAlias);
                result = BuildingBlockMap.mergeMaps(result, aliasBehavior);
            }
        } 
        return result;
    }
}



