import React from 'react';

import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import ProxyComponent from '../../common/ProxyComponent';
import '../View.css';

class HasActions extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        showSelector: false,
        selectorType: null
    };
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  refresh() {
    // force refresh
    this.closeSelector();
    this.props.refresh();
  }

  onDelete(connection) {
    console.log('Delete HAS '+ connection);
    //TODO, check if any member with this tag first..
    this.props.entity.execute("removeConnection", connection.id);
    this.refresh();
  }

  onUpdate(connection) {
    console.log('Update HAS '+ connection);
    const userEntity = this.bb.systemRoot.execute( "getEntity", 'userEntity' );
    let baseRef = connection.inherited? connection.entity : (connection.baseConnection || userEntity);
    this.showConnectionSelector(baseRef, this.onConnectionUpdateSelected.bind(this, connection));
  }

  onConnectionUpdateSelected(curRef, data, base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    let baseRef = curRef.inherited? curRef.entity : curRef.baseConnection;
    const hasEntityUpdate = entity.execute("newConnection", 
            curRef.tag, false, curRef.baseEntity, baseRef);
    this.theEntity.execute("updateConnection", hasEntityUpdate);

    if (!curRef.inherited) {
      this.theEntity.execute("removeConnection", curRef.id)
    }
    this.props.refresh();
    return true;
  }

  showConnectionSelector( selectorType, aSelectorFn ) {
    const selectorFn = aSelectorFn.bind(this);
    this.setState((prevState) => {
        return { showSelector: true, selectorType, selectorFn };
    });
  }

  renderActions(actions) {
    if (actions && actions.size > 0) {
      let list = [];
      for (let bId of actions.keys()) {
        const action = actions.get(bId);
        const name = this.formatName(action);

        //list.push(<li key={action.id} className="browse-behavior-row">
        //<span onClick={this.goTo.bind(this, action.id)} className="link">{name}</span></li>);

        const actionPresenter = action.execute("getConnection", 'connectionPresenter');
        list.push(<ProxyComponent key={action.id} 
            component={actionPresenter} 
            entity={action} 
            parent={this.theEntity}
            details={this.props.details} 
            readOnly={this.props.readOnly}
            refresh={this.props.refresh.bind(this)}
            onDelete={this.onDelete.bind(this, action)} 
            onUpdate={this.onUpdate.bind(this, action)} 
            onClick={this.goTo.bind(this, action.id)}
            history={this.props.history} />);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component) + this.getTagName();
    //const actions = this.props.entity.execute("listActions");
    const actions = this.props.entity.execute("listAllConnections", this.bb.types.systemAction);
    
    const count = actions && actions.size;
    this.props.onCount && this.props.onCount(count);
    const list = this.renderActions(actions);
    return (
      <div className='browse-behavior-container'>
        <BrowseBehaviorBox title={name} count={count}>
        {list?
          <ul className="browse-behavior-rows">
            {list}
          </ul>
          : <div className="browse-behavior-row">No Actions</div>}
        </BrowseBehaviorBox>
      </div>
    );
  }
}

export default HasActions;


