import React, { Component } from 'react';

import '../style/Common.css';
import HasBehaviors from '../view/behavior/HasBehaviors';
import HasActions from '../view/behavior/HasActions';
import HasTags from '../view/behavior/HasTags';
import IsA from '../view/behavior/IsA';
import Has from '../view/behavior/Has';
import HasInterfaces from '../view/behavior/HasInterfaces';
import CanBeA from '../view/behavior/CanBeA';
import HasAlias from '../view/behavior/HasAlias';
import IsAlias from '../view/behavior/IsAlias';
import IsIdentifiable from '../view/behavior/IsIdentifiable';
import IsSerializable from '../view/behavior/IsSerializable';
import BehaviorGroup from '../view/behavior/BehaviorGroup';

import ConnectionPresenter from '../view/connection/ConnectionPresenter';
import InlinePresenter from '../view/connection/InlinePresenter';

import Contains from '../view/behavior/Contains';
import IsContainedIn from '../view/behavior/IsContainedIn';

class ProxyComponent extends Component {
  components = {
    HasBehaviors: HasBehaviors,
    HasActions: HasActions,
    HasTags: HasTags,
    IsA: IsA,
    Has: Has,
    HasInterfaces: HasInterfaces,
    CanBeA: CanBeA,
    HasAlias: HasAlias,
    IsAlias: IsAlias,
    Contains: Contains,
    IsContainedIn: IsContainedIn,
    IsIdentifiable: IsIdentifiable,
    IsSerializable: IsSerializable,

    SystemBehavior: BehaviorGroup,
    SystemRelation: BehaviorGroup,
    UserBehavior:  BehaviorGroup,

    ConnectionPresenter: ConnectionPresenter,
    InlinePresenter: InlinePresenter
  };

  capitalizefirst(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
  }

  render() {
    const componentID = this.capitalizefirst(this.props.component.id);
    const ComponentTag = this.components[componentID];
    if (ComponentTag) {
      return (<ComponentTag {...this.props}>{this.props.children}</ComponentTag>);
    } else {
      return (<div>{componentID} Not found.</div>);
    }
  }
}

export default ProxyComponent;


