import React, { Component } from 'react';

import '../style/Common.css';

class TitleBox extends Component {
  render() {
    if (this.props.name.type) {
      return <div className="header-search-input">{this.props.name}</div>;
    } else {
      return (
        <input type="text" className="header-search-input" disabled
          data-hotkey="s" name="q" value={this.props.name} placeholder="<Entity Name>" 
          aria-label="Entity Name" autoCapitalize="off" />
      );
    }
  }
}

export default TitleBox;


