import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom'

import SearchEntities from './SearchEntities';
import ViewEntity from './ViewEntity';
import ViewEntityGraph from './ViewEntityGraph';
import ViewEntityWorkspace from './ViewEntityWorkspace';
import NoMatch from './NoMatch';

class App extends Component {
  render() {
    return (
      <Router>
        <div className="app-main">
          <Switch>
            <Route path="/search" component={SearchEntities}/>
            <Route path="/graph/:id" component={ViewEntityGraph}/>
            <Route path="/workspace/:id" component={ViewEntityWorkspace}/>
            <Route path="/view/:id" component={ViewEntity}/>
            <Route path="/view/" component={ViewEntity}/>
            <Redirect from="/" to="/search"/>
            
            <Route component={NoMatch}/>

          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;



