import React, { Component } from 'react';

class Dropdown extends Component {
    
    constructor(props) {
        super(props);
        this.documentClickHandlerFn = this.documentClickHandler.bind(this);
        this.handleKeyDownFn = this.handleKeyDown.bind(this) ;
    }

    handleKeyDown( event ) {
        if ( event.keyCode === 13 /*enter*/ ||
            event.keyCode === 27 /*esc*/ ) {
            this.close();
        }
    }

    documentClickHandler() {
        if ( this.props.visible ) {
            this.close();
        }
    }

    registerEventListeners() {
        document.addEventListener( "click", this.documentClickHandlerFn  );
        document.addEventListener( "keydown", this.handleKeyDownFn  );
    }

    unregisterEventListeners() {
        document.removeEventListener( "click", this.documentClickHandlerFn );
        document.removeEventListener( "keydown", this.handleKeyDownFn );
    }

    close() {
        this.unregisterEventListeners();
        this.props.onClose && this.props.onClose();
    }

    render() {

        if (this.props.visible) {
            this.registerEventListeners();
        } else {
            this.unregisterEventListeners();            
        }

        return (
            <span>
                { this.props.visible && 
                    this.props.children
                }
            </span>
        );
    }
}

export default Dropdown



