import React from 'react';

import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import IdInput from '../../common/IdInput';
import AliasConnection from '../../core/AliasConnection';
import utils from '../../core/utils';
import '../View.css';

class CanBeA extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        showSelector: false
    };
    this.setProps(props)
  }

  componentWillReceiveProps(nextProps) {
    this.setProps(nextProps);
  }

  setProps(props) {
    this.theEntity = (props.entity instanceof AliasConnection)? props.entity.entity : props.entity;
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onCreate(entityName) {
    const entityId = utils.generateId(entityName);
    const newEntity = this.theEntity.execute("new", entityId);
    newEntity.execute("setName", entityName);
    this.closeSelector();
    this.props.refresh();
  }

  onDelete(param) {
    this.theEntity.execute("removeSubType", param.id);
    this.closeSelector();
    this.props.refresh();
  }

  onAdd(ref) {
    this.setState((prevState) => {
        return { showSelector: true };
    });
  }

  onUpdate(entityName) {

  }

  renderSubTypes(subTypes) {
    if (subTypes && subTypes.size > 0) {
      let list = [];
      for (let bId of subTypes.keys()) {
        const subType = subTypes.get(bId);
        const name = this.formatName(subType);
        const subSubTypes = subType.execute("listSubTypes");
        const countLabel = subSubTypes && this.renderCountLabel(subSubTypes.size, subType);
        list.push(<li key={subType.id} className="browse-behavior-row">
          <span onClick={this.goTo.bind(this, subType.id)} className="link">{name}</span>
          <span key="deleteOveride" className="row-right"><button 
            className="btn-add" onClick={this.onDelete.bind(this, subType)}><i 
            className="fa fa-minus red"></i></button></span>
          {subSubTypes?<span className="row-right count">{countLabel}</span>:null}
          </li>);
      }
      return list;
    }
  }

  renderCountLabel(count, entity) {
    const aggregatedCount = count && this.getAggregatedCount(entity);
    let countLabel = aggregatedCount && (count !== aggregatedCount) && (count + '/' + aggregatedCount);
    countLabel = countLabel || count;
    return countLabel;
  }

  render() {
    const name = this.formatName(this.props.component);
    const subTypes = this.theEntity.execute("listSubTypes");
    const count = subTypes && subTypes.size;
    this.props.onCount && this.props.onCount(count);
    const countLabel = this.renderCountLabel(count, this.theEntity);
    const list = this.renderSubTypes(subTypes);
    const uid = this.bb.generateUID();
    return (
      <div className='browse-behavior-container'>
        <BrowseBehaviorBox title={name} count={countLabel}
            onAdd={this.onAdd.bind(this)}
            onUpdate={this.onUpdate.bind(this)} >
          {list?
            <ul className="browse-behavior-rows">
              {list}
            </ul>
            : <div className="browse-behavior-row">No SubTypes</div>}
        </BrowseBehaviorBox>
        <IdInput key={uid}
          visible={this.state.showSelector}
          onClose={this.closeSelector.bind(this)}
          onCreate={this.onCreate.bind(this)} />
      </div>
    );
  }
}

export default CanBeA;


