import React from 'react';

import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import IdInput from '../../common/IdInput';
import '../View.css';

class IsIdentifiable extends ViewComponent {
  constructor(props) {
    super(props);
    this.state = {
        showSelector: false
    };
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onCreate(entityName) {
    this.props.entity.execute("setName", entityName);
    this.closeSelector();
    this.props.refresh();
  }

  onDelete() {
    console.log('Delete Name ');
    this.props.entity.execute("removeName");
    this.closeSelector();
    this.props.refresh();
  }

  onAdd(ref) {
    this.setState((prevState) => {
        return { showSelector: true };
    });
  }

  render() {
    const name = this.formatName(this.props.component);
    const entityDisplay = this.formatName(this.props.entity);
    this.entityName = this.props.entity.execute("getName");
    const onAdd = this.entityName? null : this.onAdd.bind(this);
    const uid = this.bb.generateUID();
    return (
      <div className='browse-behavior-container'>
        <BrowseBehaviorBox title={name} onAdd={onAdd}>
        {this.entityName?
          <div className="browse-behavior-row">{this.entityName}
            <span key="updateName" className="row-right"><button 
              className="btn-add" onClick={this.onDelete.bind(this)}><i 
              className="fa fa-edit green"></i></button></span>
            <span key="deleteName" className="row-right"><button 
              className="btn-add" onClick={this.onDelete.bind(this)}><i 
              className="fa fa-minus red"></i></button></span>
          </div>: 
          <div className="browse-behavior-row">No Name has been set. Will be using {entityDisplay} instead.</div>}
        </BrowseBehaviorBox>
        <IdInput key={uid}
          visible={this.state.showSelector}
          onClose={this.closeSelector.bind(this)}
          onCreate={this.onCreate.bind(this)}/>
      </div>
    );
  }
}

export default IsIdentifiable;


