import React from 'react';

import AliasConnection from '../core/AliasConnection'
import ViewComponent from './ViewComponent';
import Graph from '../core/Graph';

class EntityPath extends ViewComponent {

    browse(type) {
        if (this.props.onClick) {
            this.props.onClick(type);
        } else {
            this.goTo(type.id);
        }
    }

    renderPathElement(type, active, cn) {
        const name = type.execute("getName") || "<" + type.id + ">";
        if (!active) {
            return <span className={cn}><span className="slash">/</span><span 
                className="link" onClick={this.browse.bind(this, type)}>{name}</span></span>;
        } else {
            return <span><span className="slash">/</span>{name}</span>;
        }
    }

    getEntity(entity) {
        let dEntity = entity;
        if (entity instanceof AliasConnection) {
            dEntity = entity.entity;
        } else if (entity.entity instanceof AliasConnection) {
            dEntity = entity.entity.entity;
        }
        return dEntity;
    }

    render() {
        const entity = this.props.data;
        const base = this.props.base;
        const self = this;
        let pathPrefix;
        if (entity != null) {
            let dEntity = this.getEntity(entity);
            let ancestors = dEntity.execute( "getInheritanceGraph");
            if (!ancestors.size) {
                return;
            }
            let rootObj;
            if (base) {
                let b = this.getEntity(base).id;
                rootObj = ancestors.get(b);
            } else if (ancestors.has('userType')) {
                rootObj = ancestors.get('userType');
                pathPrefix = this.renderPathElement(this.bb.root, false, "path-head");
            } else if (ancestors.has(this.bb.root.id)) {
                rootObj = ancestors.get(this.bb.root.id);
            }
            let table = [];
            let row = [];
            let idx = 0;
            let pos = true;
            const graph = new Graph(rootObj, 'children');
            let maxRowspan = 0;
            graph.traversePreorder(function(node) {
                if (!node.children) {
                    maxRowspan++;
                }
            });
            this.props.setMaxRowspan && this.props.setMaxRowspan(maxRowspan);
            /*if (maxRowspan === 1) {
                return <div className="path">{this.renderSingle()}</div>;
            }*/
            graph.traversePreorder(function(node) {
                let id = node.obj.id;
                if (node.children) {
                    let colspan = node.spos - node.pos;
                    if (!colspan || colspan < 1) {
                        colspan = 1;
                    }
                    let rowspan = (node === rootObj)? maxRowspan : 1;
                    if (!pos) {
                        const firstSpan = rootObj.pos - node.spos;
                        if (firstSpan) {
                            row.push(<td key={id+'row'} colSpan={firstSpan}></td>);
                        }
                    }
                    row.push(<td key={id} colSpan={colspan} rowSpan={rowspan} valign='top' className="nowrap">
                        {self.renderPathElement(node.obj, false)}</td>);
                    pos = true;
                } else {
                    row.push(<td key={id+'.'} valign='top' className="active nowrap">
                        {self.renderPathElement(entity, true)}</td>);
                    table.push(<tr key={idx++}>{row}</tr>);
                    row = [];
                    pos = false;
                }
            });
            return <div className="path">
                {pathPrefix}
                <table border={0}><tbody>{ table }</tbody></table>
                </div>;
        } else {
            return null;
        }
    }
}

export default EntityPath



