/**
 * BootstrappEntity.
 */
import AliasConnection from './AliasConnection';
import BuildingBlockConnection from './BuildingBlockConnection';
import BuildingBlockListMap from './BuildingBlockListMap';
import BuildingBlockMap from './BuildingBlockMap';
import CoreBuildingBlock from './CoreBuildingBlock';

export default class BootstrappEntity {
    constructor(id) {
        this.id = id;
        this.data = new Map();
        this.requiredActions = new Set(["constructor", "getCode", "get", "listSubTypes", "listAllSubTypes", "listActions", "listAllActions", "listBehaviors", "listAllBehaviors", "listAncestors", "getInheritanceGraph", "addSuperType", "listSuperTypes"]);
    }
    static getInstance() {
        BootstrappEntity.thisInstance = BootstrappEntity.thisInstance || new BootstrappEntity("v1");
        return BootstrappEntity.thisInstance;
    }
    REQUIRE(test, msg) {
        console.assert(test, msg);
    }
    _getAction(action) {
        return this[action];
    }
    _deleteAction(action) {
        if (this[action]
            && !this.requiredActions.has(action)
            && !action.startsWith("_")) {
            const p = Object.getPrototypeOf(this);
            delete p[action];
            this.log("removed bootstrap action: " + action);
            this._logActions();
        }
    }
    _logActions() {
        const functions = Object.getOwnPropertyNames(Object.getPrototypeOf(this));
        let result = "";
        for (let f of functions) {
            if (f !== "constructor" && f !== "REQUIRE" && !f.startsWith("_")) {
                result += f + ", ";
            }
        }
        this.log("bootstrap actions: " + result);
    }
    // bootstrap actions:
    log(msg) {
        console.debug(msg);
    }
    execute(action, param, extraParams) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance.REQUIRE(thisInstance, "This instance has not been bootstrapped.");
        let doExecute = true;
        // review list
        const prePostExecute = (action === "preExecute" || 
            action === "postExecute" || 
            action === "listActions" || 
            action === "getCode");
        if (!prePostExecute) {
            // might not be available
            try {
                doExecute = thisInstance.executeAction.bind(this)("preExecute", { action: action, param: param });
            }
            catch (e) { }
        }
        // execute
        let result;
        //if ( doExecute )
        try {
            result = thisInstance.executeAction.bind(this)(action, param, extraParams);
        }
        catch (error) {
            // might not be available
            try {
                doExecute = thisInstance.executeAction.bind(this)("errorExecute", { action: action, param: param, error: error });
            }
            catch (e) { }
            throw error;
        }
        if (!prePostExecute) {
            // might not be available
            try {
                return thisInstance.executeAction.bind(this)("postExecute", { action: action, param: param, result: result });
            }
            catch (e) { }
        }
        return result;
    }
    // requires getCode, listAllActions ( listAllBehaviors( listAncestors, listBehaviors ), listActions )
    executeAction(action, param, extraParams) {
        const thisInstance = BootstrappEntity.thisInstance;
        let output;
        const actionCode = thisInstance._getAction(action);
        if (actionCode) {
            output = actionCode.bind(this)(param, extraParams);
            return output;
        }
        // this will simply get actions from the behaviors, and specifically the action being asked for.
        // then pass info about the super so it can call it if needed.
        const allActions = thisInstance.listAllActions.bind(this)();
        // add actionCache and cache invalidation option.
        if (allActions && allActions.has(action)) {
            const actionInstances = allActions.get(action);
            // TODO: sort & execute
            let output;
            for (let actionInstance of actionInstances) {
                const actionCode = actionInstance.execute("getCode");
                if (actionCode) {
                    output = actionCode.bind(this)(param, output, ...extraParams);
                }
            }
            return output;
        }
        throw new Error("ACTION_NOT_FOUND: " + action);
    }
    // behavior actions:
    new(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        const newInstance = new CoreBuildingBlock(param);
        //move to isA.new ? :
        thisInstance.addSuperType.bind(newInstance)(this);
        // move to canBeA.new:
        thisInstance.addInstance.bind(this)(newInstance);
        // move this to exists
        thisInstance.data.set(param, newInstance);
        return newInstance;
    }
    // move this to storage
    getEntity(id) {
        const thisInstance = BootstrappEntity.thisInstance;
        return thisInstance.data.get(id);
    }
    getBaseType() {
        // TODO: this doesn't work with multiple parents, maybe provide a listBaseTypes as well.. 
        const thisInstance = BootstrappEntity.thisInstance;
        const ancestors = thisInstance.listAncestors.bind(this)().toArray();
        let pos = 0;
        if (ancestors.length > 1) {
            pos = Math.max(1, ancestors.length - 3);
        }
        let baseType = ancestors[pos];
        return baseType;
    }
    setCode(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance.REQUIRE(thisInstance, "Cannot auto link setCode without bootstrap support.");
        if (!param) {
            // require 
            param = thisInstance._getAction(this.id);
        }
        this.data.set("code", param);
    }
    getCode() {
        return this.data.get("code");
    }
    _addToList(param, typeValue) {
        //const thisInstance = BootstrappEntity.thisInstance;
        let typeValues = this.data.get(typeValue);
        if (!typeValues) {
            typeValues = new BuildingBlockMap();
            this.data.set(typeValue, typeValues);
        }
        typeValues.set(param.id, param);
        this.data.set(typeValue, typeValues);
    }
    _addConnectionToList(key, connectionType, param) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance.REQUIRE(param, "Nothing to add to connections");
        thisInstance.REQUIRE(connectionType, "Cannot add connection '" + param + "' without a tag");
        const connectionTypeEntity = thisInstance.data.get(connectionType);
        thisInstance.REQUIRE(connectionTypeEntity, "Connection tag '" + connectionType + "' could not be found");
        const bbConnection = new BuildingBlockConnection(param, connectionTypeEntity);
        thisInstance._addToList.bind(this)(bbConnection, key);
    }
    _getConnectionList(key, tag, testFn) {
        const connections = this.data.get(key);
        let resultList = new BuildingBlockMap();
        if (connections) {
            connections.forEach((value, key) => {
                if (value.tag && value.tag.id === tag) {
                    if (!testFn || (testFn(value))) {
                        resultList.set(value.entity.id, value.entity);
                    }
                }
            });
        }
        return resultList;
    }
    addBehavior(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance.REQUIRE(thisInstance._checkAncestors.bind(param)("systemBehavior"), "addBehavior requires a Behavior parameter.");
        thisInstance._addConnectionToList.bind(this)("connections", "systemBehavior", param);
    }
    addAction(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance.REQUIRE(thisInstance._checkAncestors.bind(param)("systemAction"), "addAction requires an Action parameter.");
        thisInstance._addConnectionToList.bind(this)("connections", "systemAction", param);
        thisInstance._deleteAction(param.id);
    }

    addInstance(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance._addToList.bind(this)(param, "subTypes");
    }
    addSuperType(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        thisInstance._addToList.bind(this)(param, "superTypes");
        // move to canBeA.new:
        thisInstance.addInstance.bind(param)(this);
    }
    listSubTypes() {
        return this.data.get("subTypes");
    }
    listSuperTypes() {
        return this.data.get("superTypes");
    }

    listBehaviors(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        return thisInstance._getConnectionList.bind(this)("connections", "systemBehavior", function (value) {
            return (!param || value.execute("listSuperTypes").has(param));
            /*if ( param ) {
                const superTypes = value.execute( "listSuperTypes" );
                if ( superTypes.has( param ) ) {
                    return true;
                }
                return false;
            }
            return true;*/
        });
    }
    listActions() {
        const thisInstance = BootstrappEntity.thisInstance;
        return thisInstance._getConnectionList.bind(this)("connections", "systemAction");
    }
    // move to storage, only needed between bootstrap functions
    listAllSubTypes(processor) {
        const thisInstance = BootstrappEntity.thisInstance;
        let result = new BuildingBlockMap();
        const subTypes = thisInstance.listSubTypes.bind(this)();
        if (subTypes && subTypes.size > 0) {
            for (let subType of subTypes.values()) {
                let foundInstance = subType;
                if (processor) {
                    thisInstance.REQUIRE(processor.execute, "The processor must have an execute().");
                    foundInstance = processor.execute("applyFilter", subType);
                }
                if (foundInstance) {
                    result.set(foundInstance.id, foundInstance);
                }
                const isAlias = (subType instanceof AliasConnection);isAlias
                //const isAlias = false;
                if ( !isAlias ) {
                    // TODO: this silently overwrites! try to detect collisions from duplicates and warn. 
                    let subInstances = thisInstance.listAllSubTypes.bind( subType )( processor );
                    result = BuildingBlockMap.mergeMaps( result, subInstances );
                }
            }
        }
        return result;
    }
    // move out
    _checkAncestors(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        const paramParent = thisInstance.listAncestors.bind(this)();
        return paramParent.has(param);
    }
    //TODO: remove
    listAncestors() {
        const thisInstance = BootstrappEntity.thisInstance;
        let ancestors = new BuildingBlockMap();
        let toProcess = [this];
        while (toProcess.length) {
            const instance = toProcess.shift();
            // prevent loops
            if (!ancestors.has(instance.id)) {
                ancestors.set(instance.id, instance);
                const directSuperTypes = thisInstance.listSuperTypes.bind(instance)();
                if (directSuperTypes) {
                    directSuperTypes.forEach((value, key) => {
                        toProcess.push(value);
                    });
                }
            }
        }
        return ancestors;
    }
    getInheritanceGraph() {
        const thisInstance = BootstrappEntity.thisInstance;
        // TODO:  deal properly with loops. This will hang in a cyclic dependency scenario.

        //thisInstance.REQUIRE(thisInstance._checkForCyclicalInheritance.bind(this)(), 
        //    "cyclical inheritance not allowed.");
        
        let ancestors = new BuildingBlockMap();
        let toProcess = [{obj: this, leaf: true}];
        
        function queueParents(i, next, pos) {
            const directSuperTypes = thisInstance.listSuperTypes.bind(i)();
            if (directSuperTypes) {
                directSuperTypes.forEach((value, key) => {
                    let children = new BuildingBlockMap();
                    children.set(next.obj.id, next);
                    toProcess.push({obj: value, children, pos: pos + 1});
                });
            } else {
                next.root = true;
            }
            return directSuperTypes;
        }
        while (toProcess.length) {
            const next = toProcess.shift();
            const obj = next.obj;
            const children = next.children;
            const pos = next.pos || 0;
            if (!ancestors.has(obj.id)) {
                queueParents(obj, next, pos);
                const spos = pos;
                ancestors.set(obj.id, {obj, children, pos, spos});
            } else {
                let inst = ancestors.get(obj.id);
                inst.children = BuildingBlockMap.mergeMaps(inst.children, children);
                inst.pos = Math.max(inst.pos, pos);
                inst.children.forEach((value, key) => {
                    value.spos = inst.pos;
                });
                queueParents(inst.obj, inst, inst.pos);
            }
        }
        return ancestors;
    }
    // required by listAllActions (rels)
    listAllBehaviors(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        let behaviors;
        const ancestors = thisInstance.listAncestors.bind(this)().toArray();
        // start from the root up
        for (let i = ancestors.length - 1; i >= 0; i--) {
            let parentBehaviors = thisInstance.listBehaviors.bind(ancestors[i])(param);
            behaviors = BuildingBlockMap.mergeMaps(behaviors, parentBehaviors);
        }
        return behaviors;
    }
    // required by executeAction (rels)
    listAllActions() {
        const thisInstance = BootstrappEntity.thisInstance;
        const behaviors = thisInstance.listAllBehaviors.bind(this)();
        let actions;
        if (behaviors) {
            for (let bInstance of behaviors.values()) {
                let bActions = thisInstance.listActions.bind(bInstance)();
                actions = BuildingBlockListMap.mergeMaps(actions, bActions);
            }
        }
        return actions;
    }
    getJSON(param) {
        const thisInstance = BootstrappEntity.thisInstance;
        let result = { id: this.id, behaviors: [] };
        const behaviors = this.execute("listAllBehaviors");
        for (let b of behaviors.values()) {
            let serializer = thisInstance.execute.bind(b)("getCode");
            if (serializer) {
                let s = serializer.bind(this)();
                if (s) {
                    result.behaviors.push(s);
                }
            }
            else {
                throw new Error(this.id + " missing serializer for " + b.id);
            }
        }
        return JSON.stringify(result, null, param);
    }
    setJSON(json) {
        const thisInstance = BootstrappEntity.thisInstance;
        const result = this.execute("new", json.id);
        for (let behaviorData of json.behaviors) {
            let behavior = this.execute("get", behaviorData.id);
            let serializer = thisInstance.execute.bind(behavior)("getCode");
            if (serializer) {
                serializer.bind(result)(behaviorData);
            }
            else {
                throw new Error (result.id + " missing serializer for " + behaviorData.id);
            }
        }
        return result;
    }
}


