import React from 'react';
import ReactDOM from 'react-dom';

import '../style/App.css';
import AppService from './AppService';
import AppMenu from '../common/AppMenu';
import AppFooter from '../common/AppFooter';
import Logo from '../common/Logo';
import TitleBox from '../common/TitleBox';
import SignInMenu from '../common/SignInMenu';
import ProxyComponent from '../common/ProxyComponent';
import EntityPath from '../common/EntityPath';
import DropDown from '../common/DropDown';
import ViewComponent from '../common/ViewComponent';
import Graph from '../core/Graph';

import SettingsPanel from '../common/SettingsPanel';
import GoldenLayout from 'golden-layout';

window.React = React;
window.ReactDOM = ReactDOM;

class ViewEntityWorkspace extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        isDropDownMenuShown: false,
        collapsedDetails: true
    };
    this.pageFocus =  true;
    
    this.setNode = this.setNode.bind(this);

    this.bb = AppService.getBuiltInBlocks();
    this.setEntity(this.props);
  }

  updateDimensions() {
    this.instance && this.instance.updateSize();
  }

  componentWillMount() {
      this.updateDimensions();
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions.bind(this));
    //this.instance && this.instance.destroy();
    this.renderDashboard();
  }

  componentWillUnmount() {
    this.pageFocus =  false;
    this.instance && this.instance.destroy();
    window.removeEventListener("resize", this.updateDimensions);
  }

  componentWillReceiveProps(nextProps) {
    this.setEntity(nextProps);
    this.instance && this.instance.destroy();
    this.renderDashboard();
  }

  setNode(node) {
    this.node = node;
  }

  setEntity(props) {
    const entityId = props.match.params.id;
    this.entity = this.bb.systemRoot.execute( "getEntity", entityId ) || this.bb.systemRoot;
  }

  showDropDownMenu(e) {
    e.preventDefault();
    e.stopPropagation();
    this.setState((prevState) => {
        return {
            isDropDownMenuShown: !prevState.isDropDownMenuShown
        }
    });
  }

  onCloseDropDownMenu() {
    if (this.pageFocus) {
      this.setState((prevState, props) => {
        return { isDropDownMenuShown: false };
      });
    }
  }

  onRefresh() {
    this.forceUpdate();
  }

  getSettingsPanelConfig() {
    return {
      id: 'settingsPanel',
      title:'Settings',
      component: 'settingsPanel',
      type: 'react-component',
      width: 25,
      props: {
        data: this.entity,

        history: this.props.history,
        location: this.props.location
      }
    };
  }

  onShowTools() {
    const root = this.instance.root;
    let container = root.contentItems[ 0 ].contentItems[ 1 ];
    var behaviorConfig =this.getSettingsPanelConfig();
    container.addChild( behaviorConfig ); 
  }

  renderMenu(entity) {
    return (
      <span className="header-entry">
        <DropDown visible={this.state.isDropDownMenuShown} onClose={this.onCloseDropDownMenu.bind(this)}>
          <div className="dropdown-menu">
            <ul className="dropdown-menu-list">
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Layout:</span>
                <ul>
                  <li className="dropdown-menu-list-item-option-selected"><span className="link"
                    onClick={this.goTo.bind(this, '/view/' + entity.id)}>List</span></li>
                  <li className="dropdown-menu-list-item-option"><span className="link"
                    onClick={this.goTo.bind(this, '/workspace/' + entity.id)}>Workspace</span></li>
                  <li className="dropdown-menu-list-item-option"><span className="link"
                    onClick={this.goTo.bind(this, '/graph/' + entity.id)}>Graph</span></li>
                </ul>
              </li>
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Settings:</span>
                <span className='link' onClick={this.onShowTools.bind(this)}>Show panel</span>
              </li>
            </ul>
          </div>
        </DropDown>
      </span>
    );
  }

  setMaxRowspan(rows) {
    const root = this.glContainer.layoutManager.root;
    let config = root.contentItems[ 0 ].contentItems[ 0 ].config;
    // TODO: fix this
    config.height = 4 + rows * 6;
    root.layoutManager.updateSize();
  }

  /**
   * Default Golden-Layout config.
   */
  getDefaultLayoutConfig() {
    return {  
      settings:{  
          hasHeaders:true,
          constrainDragToContainer:true,
          reorderEnabled:true,
          selectionEnabled:false,
          popoutWholeStack:false,
          blockedPopoutsThrowError:true,
          closePopoutsOnUnload:true,
          showPopoutIcon:false,
          showMaximiseIcon:true,
          showCloseIcon:false,
          tabControlOffset:20
      },
      dimensions:{  
          borderWidth:5,
          minItemHeight:10,
          minItemWidth:10,
          headerHeight:20, //https://stackoverflow.com/questions/39534178/golden-layout-how-to-increase-header-tabs-height
          dragProxyWidth:300,
          dragProxyHeight:200
      },
      labels:{  
          close:'close',
          maximise:'maximise',
          minimise:'minimise',
          popout:'open in new window'
      },
      content:[  
          {  
            type:'column',
            id: 'top',
            content:[  
                { id: 'entityPath', type:'component' },
                {
                  id: 'main',
                  type:'row',
                  content:[  
                    {
                      id: 'left',
                      type: "column",
                      width: 25,
                      content: [  
                        {
                          type:"stack",
                          height:25,
                          content: [ { id: "isA", type:'component' } ]
                        },
                        {  
                          type:"stack",
                          height:75,
                          content: [ { id: "canBeA", type:'component' } ]
                        }
                      ]
                    },
                    {
                      id: 'center',
                      type: "stack",
                      width: 75
                    }
                 ]
                }
            ]
          }
      ]
    };
  }

  initLayout() {
    // TODO: do this lookup properly
    const path = this.instance.root.contentItems[ 0 ].contentItems[ 0 ];

    var html = '<li class="fa fa-cog fa-sm view-settings"></li>';
    path.header.controlsContainer.prepend(html);
    const settingsDropdownBtn = path.header.controlsContainer.find( '.view-settings' );
    settingsDropdownBtn.click(this.showDropDownMenu.bind(this));
  }

  /**
   * Saves the current Golden-Layout
   */
  saveLayoutState() {
    if (!this.instance.isInitialised) {
      return;
    }
    let state = this.instance.toConfig();
    const g = new Graph(state, 'content');
    g.traversePreorder(function(node, parent) {
      if (node.type === 'component') {
        delete node.props;
      } else {
        // TODO remove this once an activeItemIndex good approach is in place
        node.activeItemIndex && delete node.activeItemIndex;
      }
    });
    var stateJSON = JSON.stringify( state );
    localStorage.setItem( 'savedState', stateJSON );
  }

  /**
   * The Entity Path Golden-Layout config.
   */
  getEntityPathConfig() {
    return {
      id: 'entityPath',
      title:'Path',
      component: 'entityPath',
      type: 'react-component',
      height: 7,
      props: {
        data: this.entity,
        setMaxRowspan: this.setMaxRowspan,

        history: this.props.history,
        location: this.props.location
      },
      isClosable: false
    };
  }

  /**
   * Creates a Golden-Layout component config.
   *
   * @param {*} behavior 
   */
  getProxyComponentConfig(behavior) {
    const name = this.formatNameAsText(behavior) || behavior.id;
    return {
      id: behavior.id,
      title: name,
      component: 'componentView',
      type: 'react-component',
      props: {
        component: behavior,
        entity: this.entity,
        details: !this.state.collapsedDetails,
        refresh: this.onRefresh.bind(this),
        onCount: this.onCount,

        history: this.props.history,
        location: this.props.location
      },
      isClosable: false
    };
  }

  /**
   * Safely delete the entry. 
   * JS Delete does not actually quite deletes the array entry and does not alter the array length.
   * 
   * @param {*} arr 
   * @param {*} index 
   */
  safeDeleteArrEntry(arr, index) {
    delete arr[index];
    let newArr = [];
    for (let entry of arr) {
      entry && newArr.push(entry);
    }
    return newArr;
  }

  /**
   * Gets a map with config entries by id. 
   * Useful to find various entries, either containers or components.
   *
   * @param {*} config 
   */
  getConfigMap(config) {
    let configMap = {};
    if (config) {
      const g = new Graph(config, 'content');
      g.traversePreorder(function(node, parent, index) {
        configMap[node.id] = node;
      });
    }
    return configMap;
  }

  /**
   * Takes the config - either the default template or the previously saved config and
   * populates it with behavior views.
   *
   * @param {*} behaviors 
   */
  setupConfig(behaviors) {
    let config;
    // gets the saved state
    const savedStateStr = localStorage.getItem( 'savedState' );
    const savedState = savedStateStr && JSON.parse( savedStateStr );
    let cfgMap = this.getConfigMap(savedState);
    // if the proper containers are found we'll use the saved state
    if (cfgMap.top && cfgMap.entityPath && cfgMap.main && cfgMap.left && cfgMap.center) {
      config = savedState;
    } else {
      // otherwise use the default layout
      config = this.getDefaultLayoutConfig();
      // and compute its map
      cfgMap = this.getConfigMap(config);
    }
    
    // init the processedBehaviors
    let processedBehaviors = {};
    for (let bId of behaviors.keys()) {
      processedBehaviors[bId] = false;
    }

    // bind the behaviors that already have a place in the config
    const self = this;
    const g = new Graph(config, 'content');
    g.traversePreorder(function(node, parent, index) {
      if (node.type === 'component') {
        let behaviorConfig;
        if (node.id === 'entityPath') {
          behaviorConfig = self.getEntityPathConfig();
        } else if (node.id === 'settingsPanel') {
          behaviorConfig = self.getSettingsPanelConfig();
        } else {
          const behavior = behaviors.get(node.id);
          if (behavior) {
            behaviorConfig = self.getProxyComponentConfig(behavior);
          } else {
            let pContent = parent.current.content;
            pContent = self.safeDeleteArrEntry(pContent, index);
            if (!pContent.length) {
              parent.parent.current.content = self.safeDeleteArrEntry(parent.parent.current.content, parent.index);
            }
          }
        }

        processedBehaviors[node.id] = true;
        node = Object.assign(node, behaviorConfig);
      }
    });

    // add new behaviors
    const container = cfgMap.center;
    let prevBehaviours = [];
    if (container.content) {
      // remove deleted items
      for (let item of container.content) {
       item &&  prevBehaviours.push(item);
      }
    }
    container.content = [];
    // add the new behaviours first 
    for (let bId of behaviors.keys()) {
      if (!processedBehaviors[bId]) {
        const behavior = behaviors.get(bId);
        const comp = this.getProxyComponentConfig(behavior);
        container.content.push(comp);
      }
    }
    container.content = container.content.concat(prevBehaviours);

    return config;
  }

  /**
   * Renders the Golden-Layout config
   */
  renderDashboard() {
    const behaviors = this.entity.execute("listAllBehaviors");
    const config = this.setupConfig(behaviors);

    this.instance = new GoldenLayout(config, this.node);
    /* register components or bind events to your new instance here */

    this.instance.on( 'initialised', this.initLayout.bind(this));
    this.instance.on( 'stateChanged', this.saveLayoutState.bind(this));

    this.instance.registerComponent( 'entityPath', EntityPath);
    this.instance.registerComponent( 'componentView', ProxyComponent);
    this.instance.registerComponent( 'settingsPanel', SettingsPanel);

    this.forceUpdate();

    this.instance.init();
  }

  onCount(count) {
    if (count && this.glContainer) {
      const countElement = `<span class='messageCounter'>${count}</span>`;
      !this.glContainer.tab.isActive && this.glContainer.tab.element.append(countElement);
      this.glContainer.on( 'tab', function( tab ){
        tab.element.append(countElement);
      });
    }
  }

  render() {
    const name = this.formatName(this.entity);
    return (
      <div className="App">
        <div className="App-header">
          <div className="d-flex px-3 flex-justify-between container-lg">
            <div>
              <Logo />
              <TitleBox name={name}/>
            </div>
            <div className="d-flex">
              <AppMenu refresh={this.onRefresh.bind(this)} />
              <SignInMenu />
            </div>
          </div>
        </div>
        <div className="App-intro">
          <div className="d-flex px-3 flex-justify-between container-lg">
              <div>
              </div>
              <div className="d-flex">
                { this.renderMenu(this.entity) }
              </div>
          </div>

          <div ref={this.setNode} className="workspace-container"/>
        </div>
        <div className="App-footer">
          <AppFooter />
        </div>
      </div>
    );
  }
}

export default ViewEntityWorkspace;



