import React from 'react';

import ViewComponent from '../../common/ViewComponent';
import ProxyComponent from '../../common/ProxyComponent';
import '../View.css';

class InlinePresenter extends ViewComponent {

  onUpdate(ref) {
    const newInlineReference = ref.execute("new", '-' + this.props.parent.id + '_' + ref.id);
    const newInlineReferenceUpdate = newInlineReference.execute("newConnection", 
      ref.tag, false, ref.baseEntity, ref.entity);
    this.props.parent.execute("updateConnection", newInlineReferenceUpdate);
    const parentName = this.props.parent.execute("getName");
    const name = ref.execute("getName");
    const newName = (parentName && name)? parentName + " " + name : null;
    if (newName) {
      newInlineReference.execute("setName", newName);
    }
    this.props.refresh();
  }

  render() {
    const entity = this.props.entity;
    const has = this.bb.types.has;
    const connectionPresenter = this.bb.types.connectionPresenter; //TODO getPresenter()
    let readOnly = this.props.readOnly || entity.inherited;// || !entity.baseConnection;
    return (
      <ProxyComponent
          component={connectionPresenter}
          entity={entity} 
          readOnly={this.props.readOnly} 
          details={this.props.details} 
          onClick={this.props.onClick}
          onDelete={this.props.onDelete} 
          onUpdate={this.onUpdate.bind(this)} 
          refresh={this.props.refresh} 
          history={this.props.history} >
            <ProxyComponent
                component={has}
                entity={entity} 
                details={this.props.details} 
                onClick={this.props.onClick}
                readOnly={readOnly} 
                refresh={this.props.refresh}
                history={this.props.history}
                hideHeader={true} />
      </ProxyComponent>
    );
  }
}

export default InlinePresenter;


