import React from 'react';

import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import '../View.css';

class IsAlias extends ViewComponent {

  render() {
    const name = this.formatName(this.props.component);
    const origin = this.props.entity.execute("getAliasOrigin");
    const originName = this.formatName(origin);

    return (
      <div className='browse-behavior-container'>
        <BrowseBehaviorBox title={name}>
        {originName?
          <ul className="browse-behavior-rows">
            <li className="browse-behavior-row"><span className="link"
          onClick={this.goTo.bind(this, origin.id)}>{originName}</span></li>
          </ul>
          : <div className="browse-behavior-row">No Origin for the Alias</div>}
        </BrowseBehaviorBox>
      </div>
    );
  }
}

export default IsAlias;


