/**
 * InheritedConnection.
 */
import BuildingBlockConnection from './BuildingBlockConnection';

export default class InheritedConnection extends BuildingBlockConnection {
    constructor(entity, tag, inherited = false, baseEntity, baseConnection, rootConnection, index) {
        super(entity, tag);
        this.entity = entity;
        this.tag = tag;
        this.inherited = inherited;
        this.baseEntity = baseEntity;
        this.baseConnection = baseConnection;
        this.rootConnection = rootConnection;
        this.index = index;
    }
}


