import React from 'react';


import AppService from '../app/AppService';
import ModalDialog from './ModalDialog';

class IdInput extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            typeId: ""
        };
        this.bb = AppService.getBuiltInBlocks();
    }
    
    handleOnChange(event) {
        this.setState({typeId: event.target.value});
    }

    handleOnCreate() {
        if ( this.props.onCreate ) {
            this.props.onCreate(this.state.typeId);
        }
    }

    handleClose() {
        if ( this.props.onClose ) {
            this.props.onClose();
        }
    }
    
    render() {
        if ( this.props.visible ) {
            return (
                <ModalDialog onClose={this.handleClose.bind(this)}>
                    <div className="modal-content-title">Provide a name for the new type you want to add</div>
                    <div className="modal-content-container">
                        <div className="entitySearchForm">
                            <input
                                type="text"
                                autoFocus
                                className="entitySearchName"
                                placeholder="Entity Name"
                                value={this.state.typeId}
                                onChange={this.handleOnChange.bind(this)}
                                />
                                <button className="btn"><span className="button-label" onClick={this.handleOnCreate.bind(this)}>Create</span></button>
                        </div>
                    </div>
                </ModalDialog> );
        } else {
            return null;
        }
    }
}

export default IdInput



