import React, { Component } from 'react';

import '../style/Common.css';

class SignInMenu extends Component {
  render() {
    return (
      <button className="header-button"><i className="fa fa-sign-in"></i><span className="sign-in-btn-label">Sign in</span></button>
    );
  }
}

export default SignInMenu;


