import BuiltInBlocks from '../core/BuiltInBlocks';

class AppService {
  static getBuiltInBlocks() {
    if (!window.semanticTypes) {
      window.semanticTypes = {};
    }
    if (window.semanticTypes.builtInBlocks) {
      return window.semanticTypes.builtInBlocks;
    } else {
      window.semanticTypes.builtInBlocks = new BuiltInBlocks();
      console.log('Start init BuiltInBlocks');
      let start = new Date().getTime();
      window.semanticTypes.builtInBlocks.init({log:false});
      let duration = new Date().getTime() - start;
      console.log('Done init BuiltInBlocks in ' + duration + 'ms');

      return window.semanticTypes.builtInBlocks;
    }
  }
}

export default AppService;


