import React, { Component } from 'react';

import '../style/Common.css';

class BrowseBehaviorBox extends Component {

  render() {
    const count = this.props.count;
    const type = this.props.type? '-' + this.props.type : '';
    return (
        <div className={'browse-behavior-box' + type}>
            <div className={'browse-behavior-header' + type}>
                {this.props.title}
                {this.props.onAdd?
                    <span className="row-right"><button className="btn-add" onClick={this.props.onAdd}><i className="fa fa-plus blue"></i></button></span>
                    : null}
                {this.props.onUpdate?
                    <span className="row-right"><button className="btn-add" onClick={this.props.onUpdate}><i className="fa fa-edit green"></i></button></span>
                    : null}
                {this.props.onDelete?
                    <span className="row-right"><button className="btn-add" onClick={this.props.onDelete}><i className="fa fa-minus red"></i></button></span>
                    : null}
                {count != null?
                    <span className={'row-right count' + type} title={count}>{count}</span>
                    : null}
            </div>
            <div className={'browse-behavior-content' + type}>
                {this.props.children}
            </div>
        </div>
    );
  }
}

export default BrowseBehaviorBox;



