import React from 'react';

import ProxyComponent from '../../common/ProxyComponent';
import BrowseBehaviorBox from '../../common/BrowseBehaviorBox';
import ViewComponent from '../../common/ViewComponent';
import TypeSelector from '../../common/TypeSelector';
import BuildingBlockMap from '../../core/BuildingBlockMap';
import AliasConnection from '../../core/AliasConnection';

class ConnectionTypePresenter extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        showSelector: false,
        selectorType: null
    };
    this.setTag(props);
  }

  componentWillReceiveProps(nextProps) {
    this.setTag(nextProps);
  }

  setTag(props) {
    this.theEntity = (props.entity instanceof AliasConnection)? props.entity.entity : props.entity;
    const tags = this.theEntity.execute("listAllConnections", props.component, {sort: true});
    const tag = tags.size? tags.values().next().value : null;
    const hasTag = tag.rootConnection || tag.entity;

    this.hasTag = hasTag;
    this.tag = tag;
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  refresh() {
    // force refresh
    this.closeSelector();
    this.props.refresh();
  }

  onConnectionSelected(data, base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    const tagRef = this.state.selectorType;
    const tag = /*tagRef.baseConnection || */tagRef.entity;
    this.theEntity.execute("addConnection", entity, tag);
    this.props.refresh();
    return true;
  }

  onConnectionTypeSelected(data, base) {
    console.log('Selected HAS TYPE '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    this.theEntity.execute("addConnection", entity, this.hasTag);
    this.props.refresh();
    return true;
  }

  onConnectionUpdateSelected(curRef, data, base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    let baseRef = curRef.inherited? curRef.entity : curRef.baseConnection;
    const hasEntityUpdate = entity.execute("newConnection", 
            curRef.tag, false, curRef.baseEntity, baseRef);
    this.theEntity.execute("updateConnection", hasEntityUpdate);

    if (!curRef.inherited) {
      this.theEntity.execute("removeConnection", curRef.id)
    }
    this.props.refresh();
    return true;
  }

  onConnectionTypeUpdateSelected(data,base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }

    const hasEntityUpdate = entity.execute("newConnection", 
            base.tag, false, base.baseEntity, base.entity);
    this.theEntity.execute("updateConnection", hasEntityUpdate);
    this.props.refresh();
    return true;
  }

  onDelete(connection) {
    console.log('Delete HAS '+ connection);
    //TODO, check if any member with this tag first..
    this.theEntity.execute("removeConnection", connection.id);
    this.refresh();
  }

  onUpdate(connection) {
    console.log('Update HAS '+ connection);
    const userEntity = this.bb.systemRoot.execute( "getEntity", 'userEntity' );
    let baseRef = connection.inherited? connection.entity : (connection.baseConnection || userEntity);
    this.showConnectionSelector(baseRef, this.onConnectionUpdateSelected.bind(this, connection));
  }

  onUpdateType(connection) {
    this.showConnectionSelector(connection, this.onConnectionTypeUpdateSelected);
  }

  onAdd(connection) {
    this.showConnectionSelector(connection, this.onConnectionSelected);
  }

  onAddType() {
    const userEntity = this.tag.entity;
    this.showConnectionSelector(userEntity, this.onConnectionTypeSelected);
  }

  showConnectionSelector( selectorType, aSelectorFn ) {
    const selectorFn = aSelectorFn.bind(this);
    this.setState((prevState) => {
        return { showSelector: true, selectorType, selectorFn };
    });
  }

  renderConnections(baseTag) {
    const connections = this.theEntity.execute("listAllConnections", baseTag, {sort: true});
    let count = 0;
    let list = [];
    let processedConnections = {};
    if (connections && connections.size > 0) {
      for (let bId of connections.keys()) {
        const connection = connections.get(bId);
        processedConnections[bId] = connection;
        //TODO fix this:
        const connectionPresenter = connection.execute("getConnection", 'connectionPresenter');
        list.push(<ProxyComponent key={connection.id} 
            component={connectionPresenter} 
            entity={connection} 
            parent={this.theEntity}
            details={this.props.details} 
            readOnly={this.props.readOnly}
            refresh={this.refresh.bind(this)}
            onDelete={this.onDelete.bind(this, connection)} 
            onUpdate={this.onUpdate.bind(this, connection)} 
            onClick={this.goTo.bind(this, connection.id)}
            history={this.props.history} />);
      }
    }
    count = list.length;
    return {list, count, processedConnections};
  }

  renderOtherConnections(connections, processedConnections, list) {
    let otherRefs = [];
    const allConnections = this.theEntity.execute("listAllConnections", null, { includeOverriden: true, sort: true });
    const allBehaviors = this.theEntity.execute("listAllBehaviors");
    if (allConnections && allConnections.size > 0) {
      
      //TODO: fix this!
      const connectionPresenter = { id: 'connectionPresenter' };

      for (let cId of allConnections.keys()) {
        const uConn = allConnections.get(cId);
        //const isAType = connections.has(uConn.id);

        let connIterator = uConn;
        let typeBelongsToAnotherBehavior = false;
        while (connIterator) {
          if (allBehaviors.has(connIterator.id)) {
            typeBelongsToAnotherBehavior = true;
            break;
          }
          if (!connIterator.tag) {
            break;
          }
          connIterator = allConnections.get(connIterator.tag.id);
        }

        if (!processedConnections[cId] && !typeBelongsToAnotherBehavior) {
          const tagName = uConn.tag? <span className="count">{this.formatName(uConn.tag)}</span> : null;

          otherRefs.push(<ProxyComponent key={uConn.id} 
            component={connectionPresenter} 
            entity={uConn} 
            details={this.props.details} 
            other={tagName}
            onClick={this.goTo.bind(this, uConn.id)} />);
        }
      }
    }
    if (otherRefs.length) {
      list.push(
        <BrowseBehaviorBox key='otherRefs' type="sub" title='Other Connections' count={otherRefs.length}>
          <ul className="browse-behavior-rows">
            {otherRefs}
          </ul>
        </BrowseBehaviorBox>
        );
    }
  }

  renderTypeLabel(newTag, crtTag, rootTag) {
    const crtTagName = this.formatName(crtTag);
    const newTagName = this.formatName(newTag);
    const rootTagName = this.formatName(rootTag);
    let details;
    if (!crtTag || crtTag.id !== newTag.id) {
      if (rootTag && (!crtTag || crtTag.id !== rootTag.id)) {
        details = <span>(now {newTagName}, originally {rootTagName})</span>;
      } else {
        details = <span>(now {newTagName})</span>;
      }
    }
    return <span>{crtTagName} types {details}</span>;
  }

  renderConnectionTypes(connections) {
    let processedConnections = {};
    let list = [];
    if (connections && connections.size > 0) {
      let tagMap = new BuildingBlockMap();
      for (let bId of connections.keys()) {
        const connection = connections.get(bId);
        const rootTag = connection.rootConnection || connection.entity;
        tagMap.set(rootTag.id, connection.entity);
      }
      for (let bId of connections.keys()) {
        const connection = connections.get(bId);
        const rootTag = connection.rootConnection || connection.entity;
        const newestTag = tagMap.get(rootTag.id);
        const isNewest = (connection.id === newestTag.id);
        const name = this.renderTypeLabel(newestTag, connection, rootTag);
        let ref = this.renderConnections(connection.entity);
        processedConnections = Object.assign(processedConnections, ref.processedConnections);
        if (isNewest || ref.count) {
        const addFn = (!this.props.readOnly && isNewest)? this.onAdd.bind(this, connection) : null;
        const updateFn = (!this.props.readOnly && isNewest)? this.onUpdate.bind(this, connection) : null;
        const deleteFn = !this.props.readOnly && !(connection.baseEntity && connection.inherited)? this.onDelete.bind(this, connection) : null;
        list.push(
          <BrowseBehaviorBox key={connection.id} type="sub" title={name} count={ref.count} 
            onAdd={addFn}
            onUpdate={updateFn}
            onDelete={deleteFn}
            readOnly={this.props.readOnly}>
              {ref.list?
                <ul className="browse-behavior-rows">
                  {ref.list}
                </ul>
                : <div className="browse-behavior-row">No Connections</div>}
            </BrowseBehaviorBox>
          );
        }
      }
    }
    this.renderOtherConnections(connections, processedConnections, list);

    return list.length && list;
  }

  render() {
    const name = <span>{this.formatName(this.props.component)} ({this.formatName(this.tag.entity)})</span>;
    const connections = this.theEntity.execute("listAllConnections", this.hasTag, { includeOverriden: true, sort: true });
    const count =  connections.size;
    this.props.onCount && this.props.onCount(count);
    const list = this.renderConnectionTypes(connections);
    const uiList = list?
      <ul className="browse-behavior-rows">
        {list}
      </ul>
      : <div className="browse-behavior-row">No Connections</div>;

    return (
      <div className='browse-behavior-container'>
        {this.props.hideHeader?
          <div>{uiList}</div>
          : 
          <BrowseBehaviorBox title={name} count={count} 
              onAdd={this.onAddType.bind(this)}>
            {uiList}
          </BrowseBehaviorBox>
        }
        <TypeSelector
            visible={this.state.showSelector}
            onClose={this.closeSelector.bind(this)}
            onSelect={this.state.selectorFn}
            type={this.state.selectorType} />
      </div>
    );
  }
}

export default ConnectionTypePresenter;


