import React from 'react';

import AppService from '../app/AppService';
import AliasConnection from '../core/AliasConnection';
import Graph from '../core/Graph';

class ViewComponent extends React.Component {

  constructor(props) {
    super(props);
    this.bb = AppService.getBuiltInBlocks();
  }

  formatName( entity ) {
    return ( entity ) ? entity.execute( "getName" ) || <i className="nowrap">&lt; { entity.id } &gt;</i> : "";
  }

  getTagName() {
    let tag;
    let allBehaviors = this.props.entity.execute("listAllBehaviors");
    if (allBehaviors.has('has')) {
      const dEntity = (this.props.entity instanceof AliasConnection)? this.props.entity.entity : this.props.entity;
      const tags = dEntity.execute("listAllConnections", this.props.component);
      tag = tags.size? tags.values().next().value : null;
    }
    
    const tagName = tag && tag.execute("getName");
    return tagName? ' (' + tagName + ')': '';
  }

  goTo(link) {
    this.props.history && this.props.history.push(link);
  }

  formatNameAsText( entity ) {
    return ( entity ) ? entity.execute( "getName" ) || '&lt; ' + entity.id + ' &gt;' : "";
  }

  getAggregatedCount(entity) {
    const entityGraph = new Graph(entity, function(curNode) {
      return curNode.execute("listSubTypes");
    });

    let countSet = new Set();
    entityGraph.traversePreorder(function(curNode, nodeParent, nodeIndex) {
      if (curNode.id !== entity.id) {
        countSet.add(curNode.id);
      }
    });
    const countSize = countSet.size;
    countSet = null;
    return countSize;
  }

  render() {
    return (
      <span />
    );
  }
}

export default ViewComponent;


