import React from 'react';

import ViewComponent from '../../common/ViewComponent';

class ConnectionPresenter extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
      details: this.props.details
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState((prevState) => {
      return {
        details: nextProps.details
      }
    });
  }

  onDelete(connection) {
    console.log('Delete HAS '+ connection);
    this.props.onDelete(connection);
  }

  onUpdate(connection) {
    this.props.onUpdate(connection);
  }

  handleDetails() {
    this.setState((prevState) => {
      return {
        details: !prevState.details
      }
    });
  }

  renderIcon(connection, error){
    if (connection.baseEntity) {
      if (connection.inherited) {
        if (connection.baseConnection) {
          return 'caret-circle-down ' + (error? 'red' : 'blue'); //inherited override
        } else {
          return 'circle ' + (error? 'red' : 'blue'); //inherited
        }       
      }
      return 'dot-circle ' + (error? 'red' : 'orange'); //override
    } else {
      return 'certificate ' + (error? 'red' : 'green'); // new ref
    }
  }

  renderActions(connection){
    if (!this.props.readOnly) {
      const deleteAction = <span key="deleteOveride" className="row-right"><button 
        className="btn-add" onClick={this.onDelete.bind(this, connection)}><i 
        className="fa fa-minus red"></i></button></span>;
      
      const updateAction = <span key="updateOveride" className="row-right"><button 
        className="btn-add" onClick={this.onUpdate.bind(this, connection)}><i 
        className="fa fa-edit orange"></i></button></span>;
      
      let result = [];
      if (connection.baseEntity) {
        result.push(updateAction);     //override
        if (!connection.inherited) {
            result.push(deleteAction); // inherited & inherited override 
        }
      } else {
        result.push(deleteAction);     // new ref
      }
      return result;
    }
  }

  renderSuperTypes(connection, postChar) {
    const superTypes = connection.execute("listSuperTypes");
    if (superTypes && superTypes.size > 0) {
      let list = [];
      for (let bId of superTypes.keys()) {
        const superType = superTypes.get(bId);
        const name = this.formatName(superType);
        list.push(<span key={superType.id}>{name}</span>);
      }
      return <span>{list
          .map(t => <span key={t.key}>{t}</span>)
          .reduce((prev, curr) => [prev, ', ', curr])}{postChar}
        </span>
    }
  }

  renderReferenceType(connection, postChar) {
    let t;
    if (connection.baseEntity) {
      if (connection.inherited) {
        if (connection.baseConnection) {
          t = connection.baseConnection;
        } else if (connection.rootConnection) {
          t = connection.rootConnection;
        } else {
          t = connection.tag;
        }
      } else {
        t = connection.baseConnection;
      }
    } else {
      t = connection.tag;
    }
    const result = this.formatName(t);
    return <span>{result}{postChar}</span>; 
  }

  renderOriginContext(connection) {
    if (connection.baseEntity) {
      const baseEntity = this.formatName(connection.baseEntity);
      if (connection.inherited) {
        if (connection.baseConnection) {
          const baseConnection = this.formatName(connection.baseConnection);
          return <span key="inherited">
            <span className='inheritedOverride'>inherits override
                </span> from {baseEntity}, base connection: {baseConnection}</span>; 
        } else {
          return <span key="inherited"><span className='inherited'>inherits
                  </span> from {baseEntity}</span>; 
        }       
      }
      const baseConnection = this.formatName(connection.baseConnection);
      const rootConnection = connection.rootConnection && 
          connection.baseConnection.id !== connection.rootConnection.id &&
        <span> ({this.formatName(connection.rootConnection)})</span>;
      return <span key="overrides"><span className='override'>overrides
        </span> {baseEntity}.<i>{baseConnection}</i>{rootConnection}</span>;
    } else {
      return <span key="overrides"><span className='new-connection'>new connection</span></span>
    }
  }

  renderConnectionDetails(connection) {
    if (this.state.details) {
      const originContext = this.renderOriginContext(connection);
      //const baseType = this.formatName(connection.execute("getBaseType"));
      return <ul key="details">
          {originContext?<li key={connection.id + '-ctx'}>{originContext}</li>:''}
          <li key={connection.id + '-ext'}>extends {this.renderSuperTypes(connection)}</li>
          {/*<li>Base Type: {baseType}</li>*/}
        </ul>;
    }
  }

  render() {
    const entity = this.props.entity;
    const name = this.formatName(entity);
    const type = (!this.props.details)? this.renderReferenceType(entity, ':') : null;
    const other = this.props.other;
    return (
      <li className="browse-behavior-row">
          <i className={"fa fa-"+this.renderIcon(entity, other)}
            onClick={this.handleDetails.bind(this) }></i> {type} <span className="link"
            onClick={this.props.onClick}>{name}</span> {other}
          {this.renderActions(entity)}
          {this.renderConnectionDetails(entity)}

          {this.props.children}
      </li>
    );
  }
}

export default ConnectionPresenter;


