import React from 'react';
import { Link } from 'react-router-dom';

import '../style/App.css';

import AppService from './AppService';

import AppMenu from '../common/AppMenu';
import AppFooter from '../common/AppFooter';
import Logo from '../common/Logo';
import TitleBox from '../common/TitleBox';
import SignInMenu from '../common/SignInMenu';
import ProxyComponent from '../common/ProxyComponent';
import EntityPath from '../common/EntityPath';
import DropDown from '../common/DropDown';
import ViewComponent from '../common/ViewComponent';

class ViewEntity extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        isDropDownMenuShown: false,
        groupedBehaviors: false,
        collapsedDetails: true
    };
    this.pageFocus =  true;

    this.bb = AppService.getBuiltInBlocks();
    this.setEntity(this.props);
  }

  componentWillUnmount() {
    this.pageFocus =  false;
    console.log('here');
  }

  componentWillReceiveProps(nextProps) {
    this.setEntity(nextProps);
  }

  setEntity(props) {
    const entityId = props.match.params.id;
    this.entity = this.bb.systemRoot.execute( "getEntity", entityId ) || this.bb.systemRoot;
  }

  showDropDownMenu() {
    this.setState((prevState) => {
        return {
            isDropDownMenuShown: !prevState.isDropDownMenuShown
        }
    });
  }

  onCloseDropDownMenu() {
    if (this.pageFocus) {
      this.setState((prevState, props) => {
        return { isDropDownMenuShown: false };
      });
    }
  }

  onGroupedBehaviors() {
    this.setState((prevState) => {
        return {
            groupedBehaviors: true
        }
    });
  }

  onFlatBehaviors() {
    this.setState((prevState) => {
        return {
            groupedBehaviors: false
        }
    });
  }

  onExpandedDetails() {
    this.setState((prevState) => {
        return {
          collapsedDetails: false
        }
    });
  }

  onCollapsedDetails() {
    this.setState((prevState) => {
        return {
          collapsedDetails: true
        }
    });
  }

  onRefresh() {
    this.forceUpdate();
  }

  renderMenu(entity) {
    let flatBehaviorClass = 'dropdown-menu-list-item-option';
    let groupedBehaviorClass = 'dropdown-menu-list-item-option';
    if (this.state.groupedBehaviors) {
      groupedBehaviorClass = 'dropdown-menu-list-item-option-selected';
    } else {
      flatBehaviorClass = 'dropdown-menu-list-item-option-selected';
    }

    let expandedDetailsClass = 'dropdown-menu-list-item-option';
    let collapsedDetailsClass = 'dropdown-menu-list-item-option';
    if (this.state.collapsedDetails) {
      collapsedDetailsClass = 'dropdown-menu-list-item-option-selected';
    } else {
      expandedDetailsClass = 'dropdown-menu-list-item-option-selected';
    }

    return (
      <span className="header-entry">
        <button className="header-button" onClick={this.showDropDownMenu.bind(this)}><i className="fa fa-cog fa-lg"></i></button>
        <DropDown visible={this.state.isDropDownMenuShown} onClose={this.onCloseDropDownMenu.bind(this)}>
          <div className="dropdown-menu">
            <ul className="dropdown-menu-list">
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Layout:</span>
                <ul>
                  <li className="dropdown-menu-list-item-option-selected"><Link 
                    to={'/view/' + entity.id}>List</Link></li>
                  <li className="dropdown-menu-list-item-option"><Link 
                    to={'/workspace/' + entity.id}>Workspace</Link></li>
                  <li className="dropdown-menu-list-item-option"><Link 
                    to={'/graph/' + entity.id}>Graph</Link></li>
                </ul>
              </li>
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Structure:</span>
                <span className={flatBehaviorClass} onClick={this.onFlatBehaviors.bind(this)}>Flat</span>
                <span className={groupedBehaviorClass} onClick={this.onGroupedBehaviors.bind(this)}>Grouped</span>
              </li>
              <li className="dropdown-menu-list-item">
                <span className="dropdown-menu-list-item-option-label">Details:</span>
                <span className={expandedDetailsClass} onClick={this.onExpandedDetails.bind(this)}>Expanded</span>
                <span className={collapsedDetailsClass} onClick={this.onCollapsedDetails.bind(this)}>Collapsed</span>
              </li>
            </ul>
          </div>
        </DropDown>
      </span>
    );
  }

  renderEntity(entity) {

    let list = [];
    let behaviorActionType = (this.state.groupedBehaviors)? "BehaviorGroups" : "Behaviors";
    const behaviors = entity.execute("listAll" + behaviorActionType);
    
    for (let bId of behaviors.keys()) {
      const behavior = behaviors.get(bId);
      list.push(
        <li key={behavior.id} className="browse-behavior list-item">
          <ProxyComponent key={behavior.id} 
            component={behavior} 
            entity={entity} 
            history={this.props.history}
            details={!this.state.collapsedDetails}
            refresh={this.onRefresh.bind(this)}></ProxyComponent>
        </li>);
    }
    return list;
  }

  render() {
    const name = this.formatName(this.entity);
    const list = this.renderEntity(this.entity);

    return (
      <div className="App">
        <div className="App-header">
          <div className="d-flex px-3 flex-justify-between container-lg">
            <div>
              <Logo />
              <TitleBox name={name}/>
            </div>
            <div className="d-flex">
              <AppMenu refresh={this.onRefresh.bind(this)} />
              <SignInMenu />
            </div>
          </div>
        </div>
        <div className="App-intro">
          <div className="d-flex px-3 flex-justify-between container-lg">
              <div className="bc-path">
                Path: <EntityPath data={this.entity} history={this.props.history} />
              </div>
              <div className="d-flex">
                { this.renderMenu(this.entity) }
              </div>
          </div>

          <ul className="main-view browse-behavior list container-lg">
            { list }
          </ul>
        </div>
        <div className="App-footer">
          <AppFooter />
        </div>
      </div>
    );
  }
}

export default ViewEntity;



