/**
 * Graph.
 */
import utils from './utils';

export default class Graph  {
    constructor(rootObj, refTag) {
        this.root = rootObj;
        if (utils.isFunction(refTag)) {
            this.getRefFn = refTag;
        } else {
            this.getRefFn = function(curNode) {
                return curNode[refTag];
            };
        }
    }

     traversePreorder(visit) {
        let nodeList = [{current: this.root}];
        while (nodeList.length) {
            let node = nodeList.pop();
            const curNode = node.current;
            visit(curNode, node.parent, node.index);
            const refs = this.getRefFn(curNode);
            if (refs) {
                const refsArr = Array.isArray(refs)? refs : refs.toArray();
                let i = 0;
                for (let ref of refsArr) {
                    nodeList.push({current: ref, parent: node, index: i++ });
                }
            }
        }
    }
}


