/**
 * BuildingBlockMap.
 */
export default class BuildingBlockMap {
    constructor() {
        this.map = new Map();
    }
    //TODO: remove k and use v.id
    set(k, v) {
        this.map.set(k, v);
    }
    get(k) {
        return this.map.get(k);
    }
    has(k) {
        return this.map.has(k);
    }
    delete(k) {
        return this.map.delete(k);
    }
    keys() {
        return this.map.keys();
    }
    values() {
        return this.map.values();
    }
    forEach(callbackfn, thisArg) {
        this.map.forEach(callbackfn, thisArg);
    }
    get size() {
        return this.map.size;
    }
    toJSON() {
        return Array.from(this.map.keys());
    }
    toArray() {
        return Array.from(this.map.values());
    }
    static mergeMaps(map1, map2, overwrite = true) {
        if (map1) {
            if (map2) {
                let newMap = new BuildingBlockMap();
                map1.forEach(function (value, key) {
                    newMap.set(key, value);
                });
                map2.forEach(function (value, key) {
                    /* TODO:
                    if ( newMap.has( key ) ) {
                        throw "BuildingBlockMap: key collision on " + key;
                    }*/
                    if (overwrite || (!overwrite && !newMap.has(key))) {
                        newMap.set(key, value);
                    }
                });
                return newMap;
            }
            else {
                return map1;
            }
        }
        else if (map2) {
            return map2;
        }
        return null;
    }

    sort(sortFn) {
        this.map = new Map([...this.map.entries()].sort(sortFn));
        return this;
    }
}


