import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import DropDown from './DropDown';
import '../style/Common.css';

import TestBuiltInBlocks from '../test/core/TestBuiltInBlocks';

class AppMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isDropDownMenuShown: false
        };
        this.pageFocus =  true;
    }

    showDropDownMenu() {
        this.setState((prevState) => {
            return {
                isDropDownMenuShown: !prevState.isDropDownMenuShown
            }
        });
    }

    closeDropDownMenu() {
        if (this.pageFocus) {
            this.setState((prevState, props) => {
                return {isDropDownMenuShown: false};
            });
        }
    }

    onLinkClick() {
        this.pageFocus =  false;
    }

    onTestDataClick() {
        console.log('Start init TestBuiltInBlocks');    
        const start = new Date().getTime();  
        new TestBuiltInBlocks().playground(window.semanticTypes.builtInBlocks);
        const duration = new Date().getTime() - start;
        console.log('Done init TestBuiltInBlocks in ' + duration + 'ms');
        this.props.refresh && this.props.refresh();
    }

    render() {
        return (
            <span className="header-entry">
                <button className="header-button" onClick={this.showDropDownMenu.bind(this)}><i className="fa fa-th fa-lg"></i></button>
                <DropDown visible={this.state.isDropDownMenuShown} onClose={this.closeDropDownMenu.bind(this)}>
                    <div className="dropdown-menu">
                        <ul className="dropdown-menu-list">
                            <li className="dropdown-menu-list-item"><Link to="/search/" onClick={this.onLinkClick.bind(this)}>Search</Link></li>
                            <li className="dropdown-menu-list-item"><Link to="/view/" onClick={this.onLinkClick.bind(this)}>Browse</Link></li>
                            <li className="dropdown-menu-list-item">TimeLine</li>
                            <li className="dropdown-menu-list-item">Theme</li>
                            <li className="dropdown-menu-list-item"><span onClick={this.onTestDataClick.bind(this)}>Test Data</span></li>
                        </ul>
                    </div>
                </DropDown>
            </span>
        );
    }
}

export default AppMenu;


