/**
 * BuildingBlockListMap.
 */
export default class BuildingBlockListMap {
    constructor() {
        this.map = new Map();
    }
    add(k, v) {
        let map = this.map.get(k);
        if (map == null) {
            map = [];
            this.set(k, map);
        }
        map.push(v);
    }
    set(k, v) {
        this.map.set(k, v);
    }
    get(k) {
        return this.map.get(k);
    }
    getAt(k, i) {
        return this.map.get(k)[i];
    }
    has(k) {
        return this.map.has(k) && this.map.get(k).length > 0;
    }
    delete(k, i) {
        if (i == null) {
            return this.map.delete(k);
        }
        else {
            return delete this.map.get(k)[i];
        }
    }
    values() {
        return this.map.values();
    }
    forEach(callbackfn, thisArg) {
        this.map.forEach(callbackfn, thisArg);
    }
    get size() {
        return this.map.size;
    }
    length(k) {
        return this.map.get(k).length;
    }
    toJSON() {
        return Array.from(this.map.keys());
    }
    static mergeMaps(map1, map2) {
        if (map1) {
            if (map2) {
                let newMap = new BuildingBlockListMap();
                map1.forEach(function (value, key) {
                    newMap.set(key, value);
                });
                map2.forEach(function (value, key) {
                    newMap.add(key, value);
                });
                return newMap;
            }
            else {
                return map1;
            }
        }
        else if (map2) {
            let newMap = new BuildingBlockListMap();
            map2.forEach(function (value, key) {
                newMap.add(key, value);
            });
            return newMap;
        }
        return null;
    }
}


