
/**
 * BuildingBlockConnection.
 */
export default class BuildingBlockConnection {
    constructor(entity, tag) {
        this.entity = entity;
        this.tag = tag;
    }
    get id() {
        return this.entity.id;
    }
    get data() {
        return this.entity.data;
    }
    execute(action, param, ...extraParams) {
        return this.entity.execute(action, param, ...extraParams);
    }
}


