import React from 'react';

//import { scaleLinear } from 'd3-scale';
//import { selectAll  } from 'd3-selection';
//import { force, forceSimulation, forceManyBody, forceLink, forceX, forceY, forceCenter } from 'd3-force';
//import { drag, dragged, dragstarted, dragended } from 'd3-drag';
import { forceSimulation, forceManyBody, forceLink, forceCenter } from 'd3-force';

class EntityGraph extends React.Component {
  constructor(props) {
  	super(props);
    this.state = {
    	nodes: props.nodes,
      links: props.links
    };
  }
  
  componentDidMount() {
    this.force = forceSimulation(this.state.nodes)
      .force("charge",
        forceManyBody()
          .strength(this.props.forceStrength)
      )
      .force("link",
        forceLink().distance(this.props.linkDistance).links(this.state.links)
      )
      .force("center", forceCenter(this.props.width / 2, this.props.height / 2));

    this.force.on('tick', () => this.setState({
    	links: this.state.links,
    	nodes: this.state.nodes
    }));

  }

  componentWillUnmount() {
    this.force.stop();
  }

  handleNodeClick(evt) {
      var svgDoc = evt.target.ownerDocument;
      
      var prev = svgDoc.getElementById(this.props.entityId);
      if (prev) {
        prev.attributes.fill.value = prev.attributes.dfill.value;
      }
      evt.target.attributes.fill.value = 'red';

      var id = evt.target.textContent;
      this.props.onClick && this.props.onClick(id);
  }

  handleMouseOver(evt) {
      //var svgDoc = evt.target.ownerDocument;
      let siblings = evt.target.parentElement.children;
      let text = siblings[1];
      text.attributes.visibility.value = 'visible';
  }

  handleMouseOut(evt) {
      //var svgDoc = evt.target.ownerDocument;
      let siblings = evt.target.parentElement.children;
      let text = siblings[1];
      text.attributes.visibility.value = 'hidden';
  }
    
  k(i, n, l) {
    return l + '_' + i;
  }

  renderLabel(node){
    if (node.x) {
      return <text visibility='hidden' fill={node.dcolor} className="text-label" 
        x={node.x - 24} y={node.y - 12}>{node.name}</text>;
    }
  }

	render() {
        
    return (
    	<svg width='100%' height={this.props.height} className="graph">
      	{this.state.links.map((link, index) =>(
          <line
            x1={link.source.x}
            y1={link.source.y}
            x2={link.target.x}
            y2={link.target.y}
            key={this.k(index, link, 'l')}
            stroke={link.color} />
        ))}
      	{this.state.nodes.map((node, index) =>(
            <g key={this.k(index, node, 'g')}>
            <circle key={this.k(index, node, 'c')} id={node.name} 
                r={node.r} cx={node.x} cy={node.y} 
                fill={node.color} dfill={node.dColor} 
                onClick={this.handleNodeClick.bind(this)} 
                onMouseOut={this.handleMouseOut.bind(this)}
                onMouseOver={this.handleMouseOver.bind(this)}>
                <title>{node.name}</title>
            </circle>
            {this.renderLabel(node)}
            </g>
        ))}
      </svg>
    );
  }
}

export default EntityGraph;



