        const { systemSemanticType, hasTags, hasAlias, isAlias, userBehavior, userAction, hasBehaviors, hasActions, isActionable, isIdentifiable, isA, has, canBeA, contains, isContainedIn, isInline, persistence, setCode, getCode, logs } = this.types;
